class Menu {
  constructor ($el) {
    this.$el = $el
    this.$toggle = $el.querySelector('.js-menu__toggle')

    if (this.$toggle) {
      this.$toggle.addEventListener('click', () => this.toggle())
      this.$drawer = $el.querySelector('x-drawer')
      this.$drawer.addEventListener('drawer:hide', () => this.toggle(false))
    }
  }

  toggle (force = null) {
    const newState = (force !== null)
      ? force
      : this.$toggle.getAttribute('aria-expanded') !== 'true'

    this.$toggle.setAttribute('aria-expanded', newState ? 'true' : 'false')
    this.$drawer.hidden = !newState
    document.documentElement.classList.toggle('is-menu-open', newState)
  }
}

export default function init (context = document) {
  context.querySelectorAll('.js-menu').forEach($el => new Menu($el))
}

init()
