/* global HTMLElement, customElements */

import * as focusTrap from 'focus-trap'
import { disableBodyScroll, enableBodyScroll } from 'body-scroll-lock'

class DrawerElement extends HTMLElement {
  static observedAttributes = ['hidden']

  constructor () {
    super()

    this.$scrollArea = this.querySelector('.js-drawer__scrollarea')
    this.$toggle = this.querySelector('.js-drawer__toggle')
    this.$toggle.addEventListener('click', () => this.toggle(false))

    this.focusTrap = focusTrap.createFocusTrap(this, {
      escapeDeactivates: true,
      clickOutsideDeactivates: true,
      onDeactivate: () => {
        this.hidden = true
      }
    })

    this.initBackdrop()
    this.addEventListener('transitionend', (e) => {
      if (this.hidden && e.propertyName === 'visibility') {
        this.dispatchEvent(new CustomEvent('drawer:hidden'))
      } else if (!this.hidden) {
        this.dispatchEvent(new CustomEvent('drawer:shown'))
      }
    })
  }

  connectedCallback () {
    document.body.insertAdjacentElement('beforeend', this.$backdrop)
  }

  disconnectedCallback () {
    document.body.removeChild(this.$backdrop)
  }

  adoptedCallback () {
    document.body.appendChild(this.$backdrop)
  }

  attributeChangedCallback (name, oldValue, newValue) {
    if (newValue === oldValue) {
      return
    }

    if (name !== 'hidden') {
      return
    }

    this.toggle(newValue === null)
  }

  toggle (force) {
    const newState = (typeof force !== 'undefined') ? force : !this.hidden

    this.$backdrop.hidden = !newState

    if (newState === true) {
      this.dispatchEvent(new CustomEvent('drawer:show'))
      this.focusTrap.activate()
      disableBodyScroll(this.$scrollArea)
    } else {
      this.dispatchEvent(new CustomEvent('drawer:hide'))
      this.focusTrap.deactivate()
      enableBodyScroll(this.$scrollArea)
    }
  }

  initBackdrop () {
    this.$backdrop = document.createElement('div')
    this.$backdrop.className = 'drawer__backdrop'
    this.$backdrop.hidden = true
  }
}

customElements.define('x-drawer', DrawerElement)
